.quality {

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  // .quality__scroll

  &__scroll {
    background-color: var(--yellow-light-color-05);
    border-radius: 6px;
    width: 320px;
    height: 12px;
  }

  .swiper-scrollbar-drag {
    background-color: var(--yellow-color);
    cursor: pointer;
  }

  // .quality__title--s

  &__title--s {}

  // .quality__slider

  &__slider {
    margin-bottom: 40px;
  }

  &__btns {
    @include flex-v-center;
    // position: absolute;
  }

  &__slider-btn {
    @include bg-cover;

    width: 40px;
    height: 40px;

    &--prev {
      background-image: url('../img/svg/left.svg');
    }

    &--next {
      background-image: url('../img/svg/right.svg');
    }

    &:not(:last-child) {
      margin-right: 14px;
    }
  }
}

.main {

  // .main__quality

  &__quality {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.slide-quality {
  width: 480px;
  @include mobile{
    width: 320px;
  }

  &__title {
    margin: 30px 0;
    @include adaptiv-font(24, 20);
    line-height: 140%;
  }

  &__image {
    width: 100%;
    aspect-ratio: 3/2;
    border-radius: var(--border-radius);
  }
}
