/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}
@mixin tablet-lg {
  @media (max-width: (1160px)) {
    @content;
  }
}
@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}
@mixin tablet-sm {
  @media (max-width: (920px)) {
    @content;
  }
}
@mixin small-tablet-lg {
  @media (max-width: (860px)) {
    @content;
  }
}
@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile-lg {
  @media (max-width: (660px)) {
    @content;
  }
}
@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
@mixin mobile-sm {
  @media (max-width: (520px)) {
    @content;
  }
}
@mixin mobile-min {
  @media (max-width: (379px)) {
    @content;
  }
}
