// !   Ш=И=Р=И=Н=А=============================================
// Адаптивная ширина (можно проставить проценты или пиксели
// @include adaptiv-width(100, 90, px);
@mixin adaptiv-width($pcSize, $mobSize, $px) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  width: calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

// Адаптивный флекс
// @include adaptiv-flex(100, 90, px);
@mixin adaptiv-flex($pcSize, $mobSize, $px) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  flex: 0 0 calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

// Адаптивная минимальная ширина (можно проставить проценты или пиксели)
// @include adaptiv-max-width(500, 200, px);
@mixin adaptiv-max-width($pcSize, $mobSize, $px) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  max-width: calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

// !   В=Ы=С=О=Т=А=============================================
// Адаптивная высота (можно проставить проценты или пиксели)
// @include adaptiv-height(100, 90, px);
@mixin adaptiv-height($pcSize, $mobSize, $px) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  height: calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

// Адаптивная минимальная высота (можно проставить проценты или пиксели)
// @include adaptiv-min-height(500, 200, px);
@mixin adaptiv-min-height($pcSize, $mobSize, $px) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  min-height: calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

// !   Ш=И=Р=И=Н=А==и=Ы=С=О=Т=А==квадрат===========================================
// @include adaptiv--w-h(500, 200, px);
@mixin adaptiv-w-h($pcSize, $mobSize, $px) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  height: calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
  width: calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

// !   П=О=З=И=Ц=И=Я====ПОСЕРЕДИНЕ==================================
// При максимальной ширине и позиционировании absolute, fixed, relative
// @include position-centre(fixed, 800);
@mixin position-centre($pos, $width) {
  position: $pos;
  top: 50%;
  left: 50%;
  max-width: $width + px;
  transform: translate(-50%, -50%);
}

// !   К=А=Р=Т=И=Н=К=А=============================================
// @include adaptiv-img-inCard(black, 100%, center);
// Адаптивная картинка в карточке (если 100%, то квадрат, чем больше, тогда вертикальная). Картинка должна находиться в div, а этот div в самой карточке, где указана ширина карточки. Например у карточки стиль такой .step {max-width: 290px;} --------- если обертка ссылка, то нужно сделать ее строчно-блочной или же она должна быть флекс элементом

@mixin adaptiv-img-inCard($color, $size, $center) {
  background-color: $color;
  padding-bottom: $size;
  position: relative;

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: $center;
  }
}

// @include img-in-circle(100, green);
// Картинка в кружочке
@mixin img-in-circle($width, $color) {
  width: $width + px;
  height: $width + px;
  background-color: $color;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// @include logo-adaptiv(100, 60);
// Логотип в обертке
@mixin logo-adaptiv($width, $height) {
  display: inline-block;
  width: $width + px;
  height: $height + px;
  background-color: transparent;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

// !   Р=А=С=Т=О=Я=Н=И=Е==У=К=А=Р=Т=О=Ч=Е=К=============================
// @include flex-w-Card(30, 25%);

// width: calc(50% - 30px);
// margin: 15px;

// Родитель flex. Ширина карточки в процентах и отступы (калькулятор). 25% это для 4 карточек на странице ===И ВСЕ РАВНО ВЫСТАВИТЬ НИЖЕ width: 25%;===
@mixin flex-w-Card($gap, $width) {
  width: calc($width - $gap);
  margin: ($gap / 2) + px;
}

