// Сдвиг вправа на уменьшение
// @include adaptiv-right(100, 90, px);
@mixin adaptiv-right($pcSize, $mobSize, $px) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  right: calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

// Сдвиг вправа на уменьшение
// @include adaptiv-top(100, 90, px);
@mixin adaptiv-top($pcSize, $mobSize, $px) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 320;
  top: calc(#{$mobSize + $px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

// скрыть скроллбар
// @include scrollbar-none();
@mixin scrollbar-none() {
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
}

