// Пропорциональный прямоугольник на всю ширину
// @include rectangle();
@mixin rectangle() {
  background-color: #333;

  &:before {
    content: '';
    padding-top: 33.333%;
    display: table;
    width: 1px;
    margin-left: -1px;
  }
}

// в див последний в списке обнуляется margin-bottom:
// @include mb(15px);
@mixin mb($margin) {
  margin-bottom: $margin;

  *:last-child {
    margin-bottom: 0;
  }
}

// в див последний в списке обнуляется margin-right:
// @include mr(15px);
@mixin mr($margin) {
  margin-right: $margin;

  *:last-child {
    margin-right: 0;
  }
}


// @import "./mixins/images";

// Cоотношение сторон 16-9 для изображения
// @include img-169();
@mixin img-169() {
  position: relative;

  &::after {
    display: block;
    content: '';
    padding-bottom: 56.25%;
  }

  img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// Иконка слева background
// @include img-169(mail.svg, 20px, 10px);
@mixin img-icon($img, $size, $gap) {
  padding-left: calc($size + $gap);
  background-image: url("../img/$img");
  background-position: left center;
  background-size: $size $size;
  background-repeat: no-repeat;
}

