html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
  color: var(--dark-color);
  font-weight: 500;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: var(--size-sx);
  color: var(--dark-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-r {
  padding-left: var(--pl-container-width);
  overflow: hidden;
}

.section-offset {
  padding: 80px 0;

  @include small-tablet {
    padding: 60px 0;
  }

  @include mobile {
    padding: 40px 0;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  column-gap: var(--offset);
  grid-template-columns: repeat(12, 1fr);

  @include mobile-lg {
    grid-template-columns: repeat(2, 1fr);
    column-gap: var(--offset-sm);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

div[class$="__btns-c"] {
  width: 100%;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 60px;

  button {
    margin: 0 auto;
  }
}

div[class$="__sub"] {
  margin: 20px 0;
  @include adaptiv-font(24, 20);
}


div[class$="__title"] {
  width: 100%;
}



div[class$="__title--s"],
div[class$="__title--m"] {
  margin-bottom: 60px;

  h2 {
    @include adaptiv-font(40, 24);
    margin-bottom: 30px;
  }

  div[class$="__desc"] {
    @include adaptiv-font(34, 20);
  }
}

div[class$="__title--s"] {
  max-width: 500px;
}

div[class$="__title--m"] {
  max-width: 850px;

  div[class$="__desc"] {
    max-width: 430px;
  }
}
