.items-order {
  &__list {
    @include mobile {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // .items-order__img

  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    max-width: 100%;
    z-index: 2;

    &--1 {
      transform: scale(.7);
      bottom: -50px;

      @include mobile {
        transform: scale(.8);
        bottom: -30px;
      }
    }

    &--2 {
      transform: scale(1.1);
    }

    &--3 {
      left: 30px;

      @include small-tablet {
        transform: scale(.8);
        bottom: -50px;
      }

      @include mobile {
        transform: scale(.6);
        left: 150px;
        bottom: -20px;
      }
    }

    &--4 {
      transform: scale(.7);
      text-align: center;
      top: -40px;
      left: -20px;

    }
  }

  // .items-order__item

  &__item {
    grid-column: 6 span;
    aspect-ratio: 1/1;
    background-color: var(--yellow-light-color);
    border-radius: var(--border-radius-b);
    padding: 50px;
    margin-bottom: var(--offset);
    position: relative;

    @include small-tablet {
      grid-column: 8 span;
      padding: 30px;
    }

    @include mobile {
      grid-column: 2 span;
    }

    h3 {
      @include adaptiv-font(60, 30);
      letter-spacing: 1px;
      margin-bottom: 40px;
      position: relative;
      z-index: 3;

      @include mobile {
        font-size: 40px;
        margin-bottom: 20px;
      }

      span {
        color: var(--yellow-color);
      }
    }

    h4 {
      @include adaptiv-font(34, 24);
      text-align: center;
      position: relative;
      z-index: 3;

      @include mobile-sm {
        font-size: 24px;
      }
    }
  }

  // .items-order__item--r

  &__item--r {
    text-align: right;
    transform: translateY(-80px);

    @include small-tablet {
      transform: translateY(0px);
    }
  }

  // .items-order__item--c

  &__item--c {
    text-align: center;
    transform: translateY(-80px);

    @include small-tablet {
      transform: translateY(0px);
    }

    h3 {
      padding: 0 30px;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      text-align: center;
    }
  }
}
