.hero {
  padding: 190px 0;
  min-height: auto;
  color: var(--light-color);
  background-image: url("../img/about/factory_1.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;

  @include small-tablet-lg {
    padding: 190px 0 100px;
  }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    z-index: 1;
  }

  // .hero__content

  &__content {
    grid-column: 6 span;
    z-index: 2;

    @include tablet {
      grid-column: 7 span;
    }

    @include mobile-lg {
      grid-column: 2 span;
    }
  }

  // .hero__about

  &__about {
    grid-column: 6 span;
    z-index: 2;

    @include tablet {
      grid-column: 5 span;
    }

    @include mobile-lg {
      grid-column: 2 span;
    }
  }
}


.content-hero {
  h1 {
    // font-size: var(--size-lgg);
    @include adaptiv-font(55, 35);
    font-weight: 700;
    color: var(--yellow-color);
    letter-spacing: 1.2px;
  }

  h2 {
    // color: var(--size-mdd);
    @include adaptiv-font(25, 20);
    margin: 40px 0 60px;
    font-weight: 500;
    line-height: 140%;

    strong {
      color: var(--yellow-color);
      font-weight: 800;
    }
  }

  // .content-hero__desc

  &__desc {
    @include adaptiv-font(55, 35);
    font-weight: 700;
    letter-spacing: 1.2px;
  }

  // .content-hero__btns

  &__btns {
    align-items: center;

    @include small-tablet-lg {
      flex-direction: column;
      align-items: start;
    }
  }
}



.about-hero {
  color: var(--dark-color);
  background-color: #fff;
  border-radius: var(--border-radius);
  @include adaptiv-font(18, 15);
  padding: 30px;
  text-align: center;
  flex-wrap: wrap;
  gap: 14px;
  justify-content: space-between;

  @include tablet {
    padding: 30px 15px 40px;
  }

  @include small-tablet-lg {
    flex-direction: column;
  }

  @include mobile-lg {
    flex-direction: row;
    justify-content: center;
  }

  @include mobile-sm {
    flex-direction: column;
  }

  // .about-hero__item

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex: 0 1 40%;

    @include small-tablet-lg {
      flex: 1;
    }

    @include mobile-lg {
      flex: 0 1 40%;
    }

    @include mobile-sm {
      flex: 1;
    }


    @include hover {
      img {
        transform: scale(1.3);
      }
    }

    img {
      width: 80px;
      height: 80px;
      transition: transform 0.3s ease-in-out;

      @include tablet {
        width: 60px;
        height: 60px;
      }
    }

    h3 {
      font-weight: 500;
      margin-top: 16px;
    }
  }
}
