// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Satoshi", sans-serif;
  --content-width: 1152px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --pl-container-width: max(var(--container-offset), calc((100% - var(--content-width)) / 2));
  --offset: 40px;
  --offset-sm: 20px;

  --size: 15px;
  --size-sx: 16px;
  --size-sm: 18px;
  --size-md: 20px;
  --size-mdd: 25px;
  --size-mdb: 34px;
  --size-lg: 40px;
  --size-lgg: 55px;
  --size-lgb: 60px;
  --lh12: 120%;
  --lh14: 140%;
  // @include adaptiv-font(55, 35);


  --border-radius: 26px;
  --border-radius-b: 32px;

  // colors
  --light-color: #fff;
  --dark-color: #0f1015;
  --dark-color-5: rgba(15, 16, 21, .5);
  --yellow-color: #f5b150;
  --yellow-light-color: #f0d8ba;
  --yellow-light-color-05: rgba(240, 216, 186, .5);
  --yellow-dark-color: #ff8562;

}
