.btn {
  background-color: var(--yellow-color);
  color: var(--light-color);
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
  padding: 16px 30px;
  outline: none;
  white-space: nowrap;
  border: none;
  cursor: pointer;
  border-radius: 34px;
  transition: background-color .3s ease-in-out;
  @include adaptiv-font(16, 14);
  display: flex;
  align-items: center;
  justify-content: center;

  @include mobile {
    padding: 16px;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    background-color: var(--dark-color-5);
  }

  &:active {
    background-color: var(--dark-color-5);
  }

  &--lg {
    color: var(--dark-color);
    @include adaptiv-font(18, 15);
    padding: 20px 40px;
    transition: background-color .3s ease-in-out, color .3s ease-in-out;

    @include hover {
      color: var(--light-color);
    }

    @include mobile {
      padding: 20px;
    }
  }

  &--b {
    @include hover {
      background-color: var(--dark-color);
    }
  }
}

.btn.focus-visible {
  outline: 1px solid var(--yellow-color);
  outline-offset: 3px;
}

.wts {

  &:hover path,
  &:focus path {
    fill: #04c956;
  }
}

.btn-video {
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  margin-left: 36px;
  display: grid;
  grid-template-columns: 70px 1fr;
  align-items: center;
  color: var(--light-color);

  @include small-tablet-lg {
    margin-left: 0;
    margin-top: 30px;
  }

  @include mobile-lg {
    margin-bottom: 60px;
  }

  span {
    display: block;
    text-align: left;
    letter-spacing: 0.5px;
    font-size: 14px;
    transition: color .3s ease-in-out;
  }

  @include hover {
    circle {
      fill: var(--dark-color);
    }

    path {
      fill: var(--yellow-color);
    }

    span {
      color: var(--yellow-color);
    }
  }

  path,
  circle {
    transition: all .3s ease-in-out;
  }
}
