.header {
  padding: 30px 0;
  // .header__container

  &__container {
    align-items: center;
    justify-content: space-between;
  }

  // header--position
  &--position {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    width: 100%;
  }

  &__logo {
    width: 160px;
    flex: 0 0 160px;
    height: 45px;
    margin-right: 20px;
  }
}



.nav {
  align-items: center;
  // .nav__list

  &__list {
    align-items: center;
    margin-right: 30px;
    gap: 20px;

    &--menu {
      @include tablet-sm {
        display: none;
      }
    }

  }

  // .nav__item

  &__item {
    &--mail {
      @include tablet-lg {
        display: none;
      }
    }

    &--phone {
      .nav__link {
        @include tablet-lg {
          font-size: 0px;
          position: relative;
          display: block;
          width: 28px;
          height: 28px;
          top: -1px;
          background-image: url('../img/svg/phone.svg');
          background-position: center;
          background-size: 28px 28px;
          background-repeat: no-repeat;
          transition: transform .3s ease-in-out;

          @include hover {
            transform: scale(1.2);
          }
        }

        @include mobile {
          display: none;
        }
      }
    }
    &--wts{
      @include mobile-lg {
          display: none;
        }
    }
  }

  // .nav__link

  &__link {
    color: var(--light-color);
    font-size: var(--size);
    transition: color .3s ease-in-out;
    white-space: nowrap;
    font-weight: 500;

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--yellow-color);
    }

    &:active {
      color: var(--yellow-color);
    }



    &--md {
      font-size: var(--size-sm);
    }
  }

  // .nav__call

  &__call {
    @include mobile {
        display: none;
      }
  }
}

.nav__link.focus-visible {
  outline: 1px solid var(--yellow-color);
  outline-offset: 3px;
}
