.formulas-list {
  gap: var(--offset);

   // .formulas-list__item

  &__item {
    grid-column: 3 span;

    @include tablet-sm {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile-lg {
      grid-column: 2 span;
    }

  }

  // .formulas-list__img

  &__img {
    margin-bottom: 20px;
    border-radius: 20px;
    overflow: hidden;
    transition: transform .3s ease-in-out;
  }

  &__link {
    display: block;
    border-radius: 20px;
    overflow: hidden;
    text-align: center;
    padding: 10px 10px 30px;
    color: var(--dark-color);
    background-color: var(--light-color);
    line-height: var(--lh14);
    transition: background-color .3s ease-in-out;

    @include hover {
      background-color: var(--yellow-light-color-05);

      .formulas-list__img {
        transform: scale(1.1);
      }
    }

    h3 {
      font-size: var(--size-sx);
      font-weight: 500;
      padding-left: 10px;
      padding-right: 10px;

      span {
        font-weight: 800;
        color: var(--yellow-color);
      }

      b {
        font-weight: 800;
      }
    }

  }
}
