.burger {
  display: none;

  @include tablet-sm {
    display: block;
    width: 44px;
    height: 44px;
    position: relative;
    margin-left: 30px;
    // .burger__line

    &__line {
      position: absolute;
      left: 50%;
      top: 50%;
      background-color: var(--light-color);
      height: 3px;
      width: 34px;
      border-radius: 20px;

      transform: translateX(-50%) translateY(-50%);

      &:first-child {
        margin-top: -10px;
      }

      &:last-child {
        left: 5px;
        margin-top: 8px;
        width: 24px;
        transform: translateY(50%);
      }
    }
  }

}
