.standarts {

  // .standarts__container

  &__container {
    padding-top: 60px;
    padding-bottom: 60px;
    text-align: center;
  }

  // .standarts__items

  &__items {
    padding: 40px 0;
  }
}

.standarts-list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;

  @include tablet-sm {
    flex-wrap: wrap;
  }

  // .standarts-list__item

  &__item {
    max-width: 130px;
    flex: 0 1 130px;

    @include tablet-sm {
      flex: 0 0 130px;
    }

    @include mobile {
      flex: 0 0 100px;
    }
  }
}
