.service {

  // .service__list

  &__list {
    @include mobile-lg {
        grid-template-columns: repeat(2, 1fr);
      }
    li {
      grid-column: 6 span;
      padding: 40px 40px 40px 0;

      h3 {
        @include adaptiv-font(30, 24);
      }

      p {
        @include adaptiv-font(24, 20);
        font-weight: 400;
      }

      @include mobile-lg{
        grid-column: 2 span;
        padding: 20px;
      }
    }

  }
}

.main {

  // .main__service

  &__service {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
